<template>
  <div>
    <div class="searchs">
      <el-form
        :inline="true"
        @submit.native.prevent="onSubmit"
        :model="searchs"
        class="form-search"
      >
        <el-row :gutter="24">
          <el-col>
            <el-form-item label="账号">
              <el-input
                v-model="searchs.keyword_search_value"
                placeholder="姓名/手机号"
              ></el-input>
            </el-form-item>
            <el-form-item label="状态">
              <el-select
                v-model="searchs.status"
                clearable
                placeholder="请选择"
              >
                <el-option
                  v-for="(value, name) in config.status"
                  :key="name"
                  :label="value"
                  :value="name"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" native-type="submit">查询</el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <el-table :data="tableData" class="list" v-loading="loading">
      <el-table-column prop="id" label="编号" align="center" />
      <el-table-column prop="channel" label="渠道" align="center" />
      <el-table-column prop="name" label="姓名" align="center" />
      <el-table-column prop="mobile" label="手机号" align="center" />
      <el-table-column prop="status_name" label="状态" align="center" />
      <el-table-column prop="money" label="奖励金额" align="center" />
      <el-table-column prop="uptime_date" label="添加时间" align="center" />
      <el-table-column prop="address" align="center" label="操作" width="300">
        <template slot-scope="scope">
          <el-button type="primary" size="small" @click="remove(scope.row.id)"
            >删除</el-button
          >
          <el-button type="warning" size="small" @click="look(scope.row.file)"
            >查看</el-button
          >
          <el-button
            v-if="scope.row.status === 'D'"
            type="success"
            size="small"
            @click="update(scope.row.id, true)"
            >通过
          </el-button>
          <el-button
            v-if="scope.row.status === 'D'"
            type="danger"
            size="small"
            @click="update(scope.row.id)"
          >
            拒绝
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
  </div>
</template>
<script>
import Fecth from '../../utils/fetch'
import { Message } from 'element-ui'

export default {
  data() {
    return {
      loading: false,
      searchs: {
        keyword_search_value: '',
        status: '',
      },
      tableData: [],
      count: 0,
      page_curren: 1,
      config: {
        type: null,
        status: null,
      },
      id: 0,
    }
  },
  mounted() {
    this.FetchList()
  },
  methods: {
    remove(id) {
      this.loading = true
      Fecth('/user/jzlist/remove', {
        id: id,
      })
        .then(() => {
          this.loading = false
          this.FetchList()
        })
        .catch(() => (this.loading = false))
    },
    FetchList() {
      this.loading = true
      Fecth('/user/jzlist/search', {
        page_curren: this.page_curren,
        ...this.searchs,
      })
        .then(r => {
          this.loading = false
          this.count = r.data.count
          this.page_curren = Math.floor(r.data.page_curren)
          this.tableData = r.data.list
          this.config = r.data.config
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
    update(id, type = false) {
      this.loading = true
      Fecth('/user/jzlist/verify', {
        id: id,
        status: type ? 'Y' : 'N',
      })
        .then(() => {
          this.loading = false
          this.FetchList()
          Message.success('更新成功')
        })
        .catch(() => (this.loading = false))
    },
    look(src) {
      this.$alert('<img style="width: 100%" src="' + src + '">', '分享截图', {
        dangerouslyUseHTMLString: true,
      })
    },
  },
}
</script>
